/**
 * ()
 */
const state = {
  isShow: false // 
}

const getters = {}
const actions = {}
const mutations = {
  /**
   * 
   * @param {Object} state : store state
   */
  showChat (state) {
    state.isShow = true
  },
  /**
   * 
   * @param {Object} state : store state
   */
  hideChat (state) {
    state.isShow = false
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
